<template>
	<view>
		<!-- //nav斑斓 -->
		<u-navbar :is-back="false" back-text="" title="随缘商店"></u-navbar>
		<!-- //nav斑斓---------- -->
		<view class="classifybody"> 
		
			<view class="leftlist">
				<view @click="clicktatbl(item,index)" v-for="(item,index) in productclass" :key="index">
					<text :class="<selectid></selectid>==index?'yesselect':''">{{item.category_name}}</text>
				</view>
			</view>
			<view class="rightblock">
				<scroll-view :lower-threshold="300">
					<u-row gutter="16">
						<u-col span="6" v-for="(item,index) in productlist" class="productlist">
							<view>
								<u-image width="100%" :src="'http://localhost:3000/imgs/'+item.product_image"
									mode="widthFix"></u-image>
							</view>
							<view class="productname">{{item.product_name}}</view>
							<view class="productintro">{{item.product_intro}}</view>
							<view class="productprice">￥{{item.product_price}}</view>
						</u-col>
					</u-row>

				</scroll-view>
			</view>
		</view>
	</view>
</template>
<script>
	export default {
		data() {
			return {
				productclass: [],
				selectid: 0,
				currentPage: 1,
				productlist: []
			}
		},
		async onLoad() {
			
			this.getproductclass()
			this.getAllProduct()
		},
		methods: {

			clicktatbl(item, index) {
				this.selectid = index;
				
			},
			//获取左边商品分类
			async getproductclass() {
				const result = await this.http.get('/product/getProductClass')
				this.productclass = result.category
				this.productclass.unshift({
					'category_id': 0,
					'category_name': "全部",
					'isselect': true
				})
			},
			//获取所有商品
			async getAllProduct() {
				const result = await this.http.post('/product/getAllProduct', {
					'currentPage': this.currentPage,
					'liststate': null
				})
				this.productlist = result.data.Product
			},
			//获取分类商品
			async getclassifyProduct() {
				const result = await this.http.post('/product/getAllProduct', {
					'currentPage': this.currentPage,
					'liststate': null
				})
				this.productlist = result.data.Product
			}
		},

	}
</script>

<style lang="scss">
	.classifybody {
		display: flex;
		width: 94%;
		margin: auto;
	}

	.leftlist {
		width: 20%;

		view {
			height: 40px;

		}
	}


	.rightblock {
		width: 80%;
	}

	.productlist {
		width: 45%;

		.productprice {
			color: red;
		}

		.productname {
			font-size: 15px;
		}

		.productintro {
			font-size: 12px;
			color: #ccc;
		}
	}

	.yesselect {
		color: red;
	}

	.noselect {
		color: #2B85E4;
	}
</style>
